import React,{useState,useEffect} from 'react'
import Card from "../components/Rescard.js"
import {Spinner,Dropdown,Button} from "react-bootstrap"
const axios = require('axios');
const Query = () => {
    const [restaurants,setRestaurants]=useState([])
    const [cuisine,setCuisine]=useState([])
    const [borough,setBorough]=useState([])
    const [term,setTerm]=useState("cuisine")
    const [value,setValue]=useState("Indian")
  const getData=()=>{
      setRestaurants([])
    axios.get(`https://enigmatic-river-29675.herokuapp.com/query/${term}/${value}`)
    .then(function (response) {
     setRestaurants(response.data.records)
    })
    .catch(function (error) {
      console.log(error);
    })
  }
    useEffect(()=>{
      axios.get(`https://enigmatic-river-29675.herokuapp.com/distinct/cuisine`)
      .then(function (response) {
       setCuisine(response.data)
      })
      .catch(function (error) {
        console.log(error);
      })
    },[])
    useEffect(()=>{
        axios.get(`https://enigmatic-river-29675.herokuapp.com/distinct/borough`)
        .then(function (response) {
         setBorough(response.data)
        })
        .catch(function (error) {
          console.log(error);
        })
      },[])
    return (
        <React.Fragment>
            <div className="row m-3">
                <div className="col-md-4 d-flex justify-content-center m-md-0 m-1">
                    <Dropdown>
                        <Dropdown.Toggle variant="success" id="dropdown-basic">
                            Cuisine
                        </Dropdown.Toggle>
                        <Dropdown.Menu>
                            {cuisine.map((item,index)=>{return <Dropdown.Item key={index} onClick={()=>{setTerm("cuisine");setValue(item)}}>{item}</Dropdown.Item>})}
                        </Dropdown.Menu>
                    </Dropdown>
                </div>
                <div className="col-md-4 d-flex justify-content-center  m-md-0 m-1">
                    <Dropdown>
                        <Dropdown.Toggle variant="success" id="dropdown-basic">
                            Borough
                        </Dropdown.Toggle>
                        <Dropdown.Menu>
                          {borough.map((item,index)=>{return <Dropdown.Item key={index} onClick={()=>{setTerm("borough");setValue(item)}}>{item}</Dropdown.Item>})}
                        </Dropdown.Menu>
                    </Dropdown>
                </div>
                <div className="col-md-4 d-flex justify-content-center  m-md-0 m-1">
                    <Button variant="success" onClick={getData}>Apply and Search</Button>
                </div>
            </div>
            <div className="row">
                {restaurants.length===0&&<div className="w-100 d-flex justify-content-center"><Spinner animation="border"/></div>}
                {restaurants.length>0&&restaurants.map((item)=>{return <Card Name={item.name} Cuisine={item.cuisine} Borough={item.borough} key={item._id} id={item._id} /> })}
            </div>
        </React.Fragment>
    )
}
export default Query
