import React,{useState,useEffect} from 'react'
import {Spinner} from "react-bootstrap"
import {useParams} from "react-router-dom"
const axios = require('axios');
const Result = () => {
    let {id}=useParams()
    const [res,setRes]=useState(null)
    useEffect(()=>{
        axios.get(`https://enigmatic-river-29675.herokuapp.com/${id}`)
        .then(function (response) {
       setRes(response.data)
        })
        .catch(function (error) {
          console.log(error);
        })
    },[id])
    return (
        <React.Fragment>
            {!res&&<div className="w-100 d-flex justify-content-center"><Spinner animation="border"/></div>}
            {res&&<div className="container text-center mt-4">
                <div>Name : {res.name}</div>
                <div>Cuisine : {res.cuisine}</div>
                <div>Borough : {res.borough}</div>
                <div>Address : {res.address.building} , {res.address.street}</div>
                <div>Grades :</div>
                {res.grades.map((item,index)=>{return <div>Grade {index+1} : {item.grade} , Score : {item.score} , Date : {item.date}</div>})}
            </div>}
        </React.Fragment>
    )
}
export default Result
