import {Navbar,Container,Nav} from "react-bootstrap"
import Restaurant from "./screens/Restaurant.js"
import Query from "./screens/Query.js";
import Result from "./screens/Result.js";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
function App() {
  return (
    <>
      <Router>
        <Navbar bg="dark" variant="dark">
          <Container>
            <Navbar.Brand href="#home">Restaurant API Test</Navbar.Brand>
            <Nav className="me-auto">
              <Link to={"/"}> <Nav.Link href="#home">All Restaurants</Nav.Link>
              </Link>
              <Link to={"/query"}> <Nav.Link href="#features">Apply Query</Nav.Link>
              </Link>
            </Nav>
          </Container>
        </Navbar>
        <Route path={"/"} component={Restaurant} exact />
        <Route path={"/query"} component={Query} exact />
        <Route path={"/result/:id"} component={Result} exact />
      </Router>
    </>
  );
}
export default App;