import React,{useEffect, useState} from 'react'
import Card from "../components/Rescard.js"
import {Spinner} from "react-bootstrap"
const axios = require('axios');
const Restaurant = () => {
  const [restaurants,setRestaurants]=useState([])
  const [page,setPage]=useState(1)
  const getData=()=>{
    axios.get(`https://enigmatic-river-29675.herokuapp.com/${(page-1)*20}/20`)
    .then(function (response) {
     setRestaurants(response.data)
    })
    .catch(function (error) {
      console.log(error);
    })
  }
    useEffect(()=>{
      axios.get(`https://enigmatic-river-29675.herokuapp.com/${(page-1)*20}/20`)
      .then(function (response) {
       setRestaurants(response.data)
      })
      .catch(function (error) {
        console.log(error);
      })
    },[page])
    return (
        <React.Fragment>
            <div className="row">
            {restaurants.length===0&&<div className="w-100 d-flex justify-content-center"><Spinner animation="border"/></div>}
              {restaurants.length>0&&restaurants.map((item)=>{return <Card Name={item.name} Cuisine={item.cuisine} Borough={item.borough} key={item._id} id={item._id} /> })}
            </div>
            <div className="container text-center py-3">
              <button type="button" className="btn btn-outline-success" onClick={()=>{page===1?setPage(1):setPage(page-1);getData();}}>{"<-"} Previous</button> 
              <p>{page}</p>
              <button type="button" className="btn btn-outline-info" onClick={()=>{setPage(page+1);getData();}}>Next {"->"}</button>
            </div>
        </React.Fragment>
    )
}
export default Restaurant
